import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import {NavAreaModule} from './components/navmodule/nav-area/nav-area.module';
import {APP_ROUTES} from './app.routing';
import {HomeComponent} from './pages/home/home.component';
import {ContactComponent} from './pages/contact/contact.component';
import {Angulartics2Module, Angulartics2GoogleAnalytics} from 'angulartics2';
import {AboutComponent} from './pages/about/about.component';
import {SkillsComponent} from './pages/myskills/myskills.component';
import {WorksComponent} from './pages/myworks/myworks.component';
import {UIPrefService} from './services/uipref.service';
import {ContentService} from './services/content.service';


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    ContactComponent,
    AboutComponent,
    SkillsComponent,
    WorksComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    NavAreaModule,
    APP_ROUTES,
    Angulartics2Module.forRoot([ Angulartics2GoogleAnalytics ])
  ],
  providers: [UIPrefService, ContentService],
  bootstrap: [AppComponent]
})
export class AppModule { }



