import {Component, Injectable} from '@angular/core';


@Injectable()
export class UIPrefService {

  private backgroundController;
  private backgroundAnimation = true;

  setBackgroundCanvasController(controller: Component) {
    this.backgroundController = controller;
  }

  toggleBackgroundAnimation(): void {
    this.backgroundController.toggleBackgroundAnimation();
  }

  setBackgroundAnimationState(shouldAnimate: boolean): void {
    this.backgroundController.setBackgroundAnimationState(shouldAnimate);
  }

}



