import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';


export class WorkDetail {

  constructor();
  constructor(
    public title = '',
    public detail = '',
    public timestring = '',
    public link?: string,
    public img?: string
  ) {}

}


@Injectable()
export class ContentService {

  GetAllWorks(): Observable<[WorkDetail]> {
    return new Observable<WorkDetail[]>((observer) => {
      let request = new XMLHttpRequest();
      request.onload = function() {
        observer.next(JSON.parse(request.responseText));
      };
      request.open('get', '../../assets/works.json' + '?cache=' + (Math.random() * 1000000), true);
      request.send();
    });
  }

}



