import {Component} from '@angular/core';
import {NavModuleService} from '../../components/navmodule/navmodule.service';

@Component({
  selector: 'home-page',
  templateUrl: './home.html',
  styleUrls: ['./home.css']
})

export class HomeComponent {

  constructor(
    private navModuleService: NavModuleService,
  ) {}

  OnMainButtonClicked(event): void {
    this.navModuleService.toggleDrawer();
  }

}



