import {Component, Input, OnInit, Output} from '@angular/core';
import {Router} from "@angular/router";
import {NavModuleService} from '../navmodule.service';

@Component({
  selector: 'nav-area',
  templateUrl: './nav-area.html',
  styleUrls: ['./nav-area.css']
})

export class NavArea implements OnInit{

  @Input()
  isDrawerExpanded = false;

  constructor(
    private router: Router,
    private navModuleService: NavModuleService
  ) {}

  ngOnInit(): void {
    // register itself to NavModuleService so it can be controlled
    // there is probably a better way to do this, let the service init NavArea... but whatever
    this.navModuleService.setNavArea(this);
  }

  setDrawerExpand(shouldExpand) {
    this.isDrawerExpanded = shouldExpand;
  }

  toggleDrawer() {
    this.isDrawerExpanded = !this.isDrawerExpanded;
  }

  toContact() {
    this.router.navigate(['/contact']);
  }


}



