import {Component, OnInit} from '@angular/core';
import {Angulartics2GoogleAnalytics} from 'angulartics2';
import {UIPrefService} from './services/uipref.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  public backgroundAnimation = true;

  constructor(
    angulartics2GoogleAnalytics: Angulartics2GoogleAnalytics,
    private uiPrefService: UIPrefService
  ) {}

  ngOnInit(): void {
    particlesJS.load('particles-js', '../assets/particles.json', null);
    this.uiPrefService.setBackgroundCanvasController(this);
  }

  toggleBackgroundAnimation(): void {
    this.backgroundAnimation = !this.backgroundAnimation;
  }

  setBackgroundAnimationState(shouldAnimate: boolean): void {
    this.backgroundAnimation = shouldAnimate;
  }

}



