import {Component, EventEmitter, Input, Output} from '@angular/core';
import {Router} from '@angular/router';
import {UIPrefService} from '../../../services/uipref.service';

@Component({
  selector: 'nav-drawer',
  templateUrl: './nav-drawer.html',
  styleUrls: ['./nav-drawer.css']
})

export class NavDrawerComponent {

  public shouldAnimateBackground = true;

  navigationItems = [
    { title: 'Home', router_link: 'home' },
    { title: 'About Me', router_link: 'about' },
    { title: 'My Skills', router_link: 'myskills' },
    { title: 'My Works', router_link: 'myworks' }
  ];
  @Input() isExpanded = false;
  @Output() navItemClicked = new EventEmitter();

  constructor(
    private router: Router,
    private uiPrefService: UIPrefService
  ) {}

  navigate(dest): void {
    this.router.navigate([dest]);
    this.navItemClicked.emit();
  }

  OnBackgroundAnimationStateChanged(event): void {
    this.uiPrefService.setBackgroundAnimationState(this.shouldAnimateBackground);
  }

}



