import {Component, OnInit} from '@angular/core';
import {ContentService, WorkDetail} from '../../services/content.service';

@Component({
  selector: 'myworks-page',
  templateUrl: './myworks.html',
  styleUrls: ['./myworks.css']
})

export class WorksComponent implements OnInit {

  private shouldHideWork = false;

  private workDetails: [WorkDetail];

  constructor(
    private contentService: ContentService
  ) {}

  ngOnInit(): void {

    if (!this.shouldHideWork) {
      this.contentService.GetAllWorks().subscribe((workDetails) => {
        this.workDetails = workDetails;
      }, (error) => {
        console.log(error);
      });

    } else {
      // prevent workDetails from being load
      this.workDetails = null;
    }
  }

}



