
import {Injectable} from '@angular/core';

@Injectable()
export class NavModuleService {

  private navArea;

  setNavArea(navArea) {
    this.navArea = navArea;
  }

  toggleDrawer(): void {
    this.navArea.toggleDrawer();
  }

  setDrawerState(shouldExpand): void {
    this.navArea.setDrawerExpand(shouldExpand);
  }

}




